/* Profile Page Container */
.logoutContainer {
    display: flex;
    justify-content: right;
    align-items: center;
    margin-top: 100px;
  }

.profileContainer {
    width: 100%;
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* Profile Header */
  .profileHeader {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .profilePic {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .profileInfo h2 {
    font-size: 1.5rem;
    color: #333;
  }
  
  .profileInfo .bio {
    font-size: 1rem;
    color: #666;
    margin-top: 10px;
  }
  
  /* Profile Details */
  .profileDetails {
    margin-top: 20px;
  }
  
  .profileDetails h3 {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 10px;
  }
  
  .profileDetails p {
    font-size: 1rem;
    color: #555;
    margin: 5px 0;
  }
  
  .profileDetails strong {
    font-weight: bold;
  }

  .toDo {
    color: #bc0808;
  }

  .input {
    border-radius: 5px;
    border: 1px solid #ccc;
    padding: 6px;
    margin-right: 2px;  
  }

  .saveNameButton{
    padding: 5px;
    margin-left: 3px;
    background-color: #7CA1CC;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
  }
  
  .deleteUserButton {
    margin-top: 5%;
    padding: 6px;
    background-color: #F05039;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
  }
  
  .successMessage {
    background-color: #7CA1CC; /* Green background for success */
    color: white;
    padding: 15px;
    border-radius: 10px;
    font-size: 1.2rem;
    text-align: center;
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: fadeIn 1s ease-in-out; /* Smooth fade-in effect */
    font-weight: bold;
  }

  /*invitation styling*/ 
  .invitationContainer {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.invitationContainer button {
  padding: 10px 16px;
  margin-right: 8px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.2s;
}

.invitationContainer button:hover {
  filter:brightness(0.8);
}

.modalButtons {
  padding: 5px;
  color: white;
}

.confirmBtn {
  padding: 6px;
  background-color: #F05039;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.cancelBtn {
  margin-left: 2%;
  padding: 6px;
  background-color: gray;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}
  
  /* Fade-in animation for success message */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  /* Success message responsive handling */
  @media (max-width: 768px) {
    .successMessage {
      font-size: 1rem; /* Adjust size on smaller screens */
      padding: 12px;
    }
  }
  
  @media (max-width: 480px) {
    .successMessage {
      font-size: 0.9rem;
      padding: 10px;
    }
  }